    height: 25vw;
  }

  .after-product {
    padding-top: 2%;
  }
  h3 {
    color: #e2576c;
    text-align: center;
    display: block;
    font-style: italic;
    margin-bottom: 1.5rem;
    font-size: 23px;
  }

  .item-tag {
    font-family: "Playfair Display Web", serif;
    line-height: 1;
    color: #e2576c;
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  #mainform {
    display: flex;
  }

  .email-form {
    display: flex;
    justify-content: center;
    display: block;
    margin: 2rem auto 0;
    position: relative;
    width: 67%;
  }

  input[type="email"] {
    padding: 0.5rem 1.75rem;
    padding-right: 150px;
    width: 100%;
  }

  input[type="submit"] {
    background-color: #e2574c;
    color: #fff;
    cursor: pointer;
    font-family: "Raleway Web", sans-serif;
    font-weight: 300;
    padding: 0.25rem 1.5rem;
    position: absolute;
    right: 0.25rem;
    top: 0.25rem;
  }

  .flex-container {
    height: 40%;
    display: flex;
    text-align: unset;
  }
  .social {
    display: block;
  }
  .facebook {
    display: contents;
  }

  .twitter {
    display: contents;
  }
  .instagram {
    display: contents;
  }
  .pinterest {
    display: contents;
  }

  /* THIS IS THE WIDGET AREA */

  @media screen and (min-width: 600px) {
    .carousel-cell {
      width: 25%;
    }

    .main-carousel {
      margin-bottom: 100px;
      padding-bottom: 75px;
    }

    .item-tag {
      font-family: Playfair Display Web, serif;
      line-height: 1;
      color: #e2576c;
      font-size: 1.5rem;
      margin-bottom: 1rem;
    }

    .Products-type {
      font-weight: 300;
    }

    .price {
      color: #969696;
      font-weight: 300;
    }

    .flex-container {
      padding: 58px;
    }
  }
}
